import * as actionType from './actionType'
import {getData} from 'util'
import {API_PRODUCT_STOCK_IMPORT} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.DATAINIT_UPDATE,
  data: object
})

//异步

// 提交
export const handleSubmit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.DATAINIT_UPDATE, data: {tableLoading: true}})
  getData({api: API_PRODUCT_STOCK_IMPORT, ...reqData}).then(data => {
    // 相应的逻辑判断    
    dispatch({
      type: actionType.DATAINIT_UPDATE,
      data: {
        tableLoading: false,
        searchResult: []
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.DATAINIT_FILE
    })
  })
}


