import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import modal from '../redux/modal'

@connect(
  state => ({...state.dataInit, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '货主编号',
      width: 150,
      dataIndex: 'shipperCode',
      key: 'shipperCode'
    }, {
      title: '地点编号',
      width: 200,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode'
    }, {
      title: '库区编号',
      width: 150,
      dataIndex: 'reservoirAreaCode',
      key: 'reservoirAreaCode'
    }, {
      title: '储位编号',
      width: 150,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode'
    }, {
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '期初库存量',
      width: 100,
      dataIndex: 'qty',
      key: 'qty'
    }]
  }

  handleOperator(text, record, index, type) {
    const {handleGetList, searchParams, page, size, handleOperator} = this.props
    confirm({
      title: '温馨提示',
      content: `是否${type ? '启用' : '禁用'}该条信息？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          data: {
            id: record.id,
            warehouseStatus: type
          },
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // onEdit(text, record, index) {
  //   const {update} = this.props
  //   update({isShowModal: true, modalTitle: '商品物流信息维护', modalData: record})
  // }
  componentDidMount() {
    // const {handleGetList, searchParams, page, size, userLoation} = this.props
    // if (!userLoation || !userLoation.value ) return
    // handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {update, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      update(modal)
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    console.log(this.props)
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={false}
        />
      </div>
    )
  }
}



