import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {Import} from 'HOC'
import {
  API_PRODUCT_STOCK_PREIMPORT
} from 'api'
import modal from '../redux/modal'

@connect(
  state => ({...state.dataInit}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {update} = this.props
    update({tableLoading: false, searchResult: res.result.data})
    res && res.message === 'ok' && message.success('导入成功')
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

  // 提交
  handleSubmit = () => {
    const {handleSubmit, searchResult, page, size} = this.props
    handleSubmit({data: searchResult}, () => {
      message.success('提交成功')
    })
  }

  // 清空数据
  clean = () => {
    const {update} = this.props
    update(modal)
  }
  render() {
    const {searchResult} = this.props
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={() => {
          this.clean()
        }}>清空数据</Button>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T04')
        }}>下载模板</Button>
        <Import
          action={{api: API_PRODUCT_STOCK_PREIMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>导入</Button>
        </Import>
        <Button type="primary" className={style.btn} disabled={!searchResult.length} onClick={() => {
          this.handleSubmit()
        }}>提交</Button>
      </div>
    )
  }
}



